*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body{
  font-family: 'montseratt', sans-serif;
}

.app{
  background-image: url(./Assets/cold.jpeg);
  background-size: cover ;
  background-position: bottom;
  transition: 0.4s ease-out;
}
.app.warm{
  background-image: url(./Assets/warm.jpeg);
  
}
main{
  min-height: 100vh;
  background-image: linear-gradient(to bottom, rgba(0,0,0,0.2), rgba(0,0,0,0.6));
  padding: 25px;
}

.search-box{
  margin: 0 0 75px;
  position: relative;
  top: -16px;
}

.search-bar{
  padding: 0.7rem;
  border: none;
  outline: none;
  width: 100%;
  border-radius: 0 0 16px 16px;
  margin-top: -25px;
  background-color: rgba(255,255,255,0.5);
  box-shadow: 0px 3px 15px rgba(0,0,0,0.2);
  color: #000000;
  font-size: 20px;
  transition: 0.4s ease;
}

.search-box .search-bar:focus{
  background-color: rgba(255,255,255,1);
}

.location-box .location {
  color: rgb(159, 233, 255);
  font-size: 32px;
  text-align: center;
  font-weight: 700;
  text-shadow: 0px 2px 15px rgba(0,0,0,0.2);
  margin: 1rem;
}
.location-box .date {
  color: rgb(255, 255, 255);
  font-size: 22px;
  text-align: center;
  font-weight: 300;
  /* font-style: oblique; */
  text-shadow: 0px 1px 15px rgba(0,0,0,0.2);
  margin: 1rem;
}

.weather-box{
  text-align: center;
}


.weather-box .temp{
  background-color: rgba(255, 255, 255, 0.3);
  text-shadow: 0px 2px 15px rgba(0,0,0,0.2);
  color: white;
  margin: 30px auto;
  padding: 30px 0px;
  width: 70%;
  font-size: 80px;
  font-weight: 700;
  border-radius: 20px;
  box-shadow: 0px 3px 15px rgba(0,0,0,0.2);
}

.weather-box .weather{
  background: linear-gradient(to bottom, rgb(89, 216, 255),rgb(0, 180, 235) );
  -webkit-text-fill-color: transparent;
  -webkit-background-clip: text;
  font-size: 50px;
  font-weight: 700;
  text-shadow: 0px 2px 15px rgba(0,0,0,0.2);
}